html {
  font-size: 10px;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  max-width: 100vw;
  min-height: 100vh;
  width: 100%;
  height: 100%;
}

a, 
a:visited,
a:hover,
a:active {
  text-decoration: none;
  color: inherit;
  font-weight: inherit;
}

.column {
  display: flex;
  flex-direction: column;
}

.row {
  display: flex;
  flex-direction: row;
}